#seo-backlink-monitor {
  .card {
    display: inline-block;
    &:not(.show) {
      display: none;
    }
  }

  p.submit {
    text-align: right;

    &::after {
      clear: both;
      display: block;
      content: '';
      height: 0;
      margin-bottom: -10px;
    }
  }

  #add-multiple-valid-links {
    td {
      padding-right: 0;
      padding-left: 0;
    }

    textarea {
      width: 100%;
    }
  }
}

#seo-backlink-monitor-links {
  background: #fff none repeat scroll 0 0;
  border: 1px solid #e5e5e5;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
  margin-top: 20px;
  min-width: 255px;
  padding: 0.7em 2em 1em;
  position: relative;
  clear: both;


  .dashicons {

    &.spin {
      animation: dashicons-spin 1s infinite;
      animation-timing-function: linear;
    }

    &.dashicons-admin-site {
      color: #3d74f9;

      &.down {
        color: #ffbb00;
      }
    }

    &.dashicons-editor-unlink,
    &.dashicons-format-status {
      color: #b42525;
    }

    &.dashicons-format-status {
      &::after {
        content: attr(title);
        font-size: 10px;
        margin-right: -12px;
        display: block;
        font-family: sans-serif;
        margin-top: -4px;
      }
    }

    &.dashicons-desktop-yes,
    &.dashicons-smartphone-yes {
      color: green;
    }

    &.dashicons-desktop-no,
    &.dashicons-smartphone-no {
      color: #c35151;
    }

    &.dashicons-smartphone-unchecked {
      color: #969696;
    }
  }

  .extended {
    display: none;
  }

  .notes {
    margin: 6px -8px;
    padding: 5px 8px;
    background: #dae8ef;
  }

  .toggle-note {
    display: none;
  }
}

#refresh-all-btn {
  float:right;

  .dashicons {
    margin: 0 3px 0 -2px;
    line-height: 1.8;
  }
}

/* Table Headlines */
#dateRefresh {
  width: 10% !important;
}
#status {
  width: 60px !important;
}
#follow {
  width: 60px !important;
}
#actions {
  width: 80px !important;
}

/* Table TDs */
.column-dateRefresh {
  .icons {
    position: absolute;
    top: 7px;
    right: 50px;
  }
}
.column-status,
.column-follow {
  text-align: left
}
tbody {
  .column-follow {
    white-space: nowrap;
  }
}

.column-actions {
  text-align: right;

  a {
    margin: 0 1px;

    &:focus {
      box-shadow: none !important;
    }

    .dashicons {
      :hover {
        color:#a00 !important;
      }
    }

    &[class*=refresh] {
      :hover {
        color: green !important;
      }
    }
  }
}

/* SEARCH */

#seo-blm-links-search-btn {
  margin-right: 5px;
}

.seo-blm-field-required {
  border-color:#a00 !important;
}

//
// BREAKPOINTS
//
@media screen and (max-width: 782px) {
  #seo-backlink-monitor-links {
    .mobile .linkTo {
      + .toggle-extended + .extended {
        margin-bottom: -16px;
      }
    }
  }
  .search-help {
    display: none!important;
  }
}

@media screen and (min-width: 783px) {
  #seo-backlink-monitor {
    .mobile {
      display:none;
    }

    #add-multiple-valid-links {
      thead th {
        padding-top: 10px;
        padding-bottom: 0;
      }
    }
  }

  #seo-backlink-monitor-links {
    &.hide-notes {
      .notes {
        display: none;
      }
      .toggle-note {
        display: inline-block;
        float: right;
        color: #aaa;
        margin-left: 7px;
      }
    }
  }

  #refresh-all-btn {
    position: relative;
    top: -5px;
    font-size: .9em;

    .dashicons {
      line-height: 1.6;
    }
  }

  .search-help {
    display: none;
    margin-bottom: 10px;
    clear: both;

    li:not(:last-child) {
      margin-right: 15px
    }
    a {
      .dashicons {
        position: relative;
        top: 3px;
      }
    }
  }
}

@keyframes dashicons-spin {
  0% {
    transform: rotate( 0deg );
  }
  100% {
    transform: rotate( 360deg );
  }
}
